import { useState } from 'react';
import { IconButton, makeStyles, withStyles } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import ShareIcon from '@material-ui/icons/Share';
import EjesModal from './ejesModal';
import DashboardIcon from '@material-ui/icons/Dashboard';
import SimpleMap from './map';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import Drawer from '@material-ui/core/Drawer';
import { Grow, Fade } from '@material-ui/core';
import colores from '../styles/colors';
import Fab from '@material-ui/core/Fab';
import Paper from '@material-ui/core/Paper';
import ProyectosModal from './proyectModal'
import Cardd from './card';

const NavigationBar = (props) => {
	const classes = useStyles();
	const [value, setValue] = useState(null);
	const [mapHandler, setMapHandler] = useState(false);
	const [ejesHandler, setEjesHandler] = useState(false);
	const [socialHandler, setSocialHandler] = useState(false);
	const [proyectHandler, setProyectHandler] = useState(false);

	const mainButtons = [
		{
			icon: <ShareIcon />,
			name: 'mis redes',
			func: () => setSocialHandler(true),
		},
		{
			icon: <DashboardIcon />,
			name: 'ejes de campaña',
			func: () => setEjesHandler(true),
		},
		{
			icon: <BookmarksIcon />,
			name: 'proyectos',
			func: () => setProyectHandler(true),
		},
	];

	return (
		<div
			style={{
				position: 'absolute',
				bottom: 30,
				alignSelf: 'center',
			}}
		>
			<BottomNavigation
				value={value}
				onChange={(event, newValue) => {
					setValue(newValue);
				}}
				className={classes.paperMain}
			>
				{mainButtons.map((btn, i) => (
					<BottomNavigationAction
						icon={
							<Grow in={true} timeout={1100 * i + 1100}>
								<Fab aria-label={btn.name} key={btn.name}>
									{btn.icon}
								</Fab>
							</Grow>
						}
						showLabel
						onClick={btn.func}
						label={
							<Paper
								style={{
									marginTop: 5,
									padding: 6,
									letterSpacing: 2.4,
								}}
							>
								{btn.name}
							</Paper>
						}
						value={i}
					/>
				))}
			</BottomNavigation>
			<Location
				state={mapHandler}
				handlerClose={() => {
					setMapHandler(false);
					setValue(null);
				}}
			/>
			<EjesModal
				open={ejesHandler}
				handleClose={() => {
					setEjesHandler(false);
					setValue(null);
				}}
			/>

			<ProyectosModal open={proyectHandler} handleClose={() => {
					setProyectHandler(false);
					setValue(null);
				}}   />
			
			<SocialBar
				openHandler={socialHandler}
				closeHandler={() => {
					setSocialHandler(false);
					setValue(null);
				}}
			/>
		</div>
	);
};

const Location = ({ state, handlerClose }) => {
	return (
		<Drawer anchor={'top'} open={state} onClose={handlerClose}>
			<div style={{ height: 500 }}>
				<SimpleMap />
			</div>
		</Drawer>
	);
};

const SocialBar = ({ openHandler, closeHandler }) => {
	return (
		<Drawer
			anchor='left'
			open={openHandler}
			onClose={closeHandler}
		
		>
			<div 	style={{ width: 400, paddingLeft: 40, justifyContent: 'space-between' }}> 
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
			<div style ={{marginBottom: 20}}>
			<Cardd  />
			</div>
		
			
			</div>
		</Drawer>
	);
};


const useStyles = makeStyles(() => ({
	circularBtn: {
		borderWidth: 2,
		borderColor: 'blue',
	},
	paperMain: {
		marginRight: 'auto',
		marginLeft: 'auto',

		backgroundColor: colores.mainDark,
		width: isMobile ? 290 : 420,
		borderRadius: 15,
	},
}));

export default NavigationBar;
