import React, { forwardRef } from 'react';
import { Grid, IconButton, makeStyles } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import gen from '../assets/ejes/genn.jpg';
import obr from '../assets/ejes/obr.png';
import ecoAm from '../assets/ejes/maff.png';
import noi from '../assets/noimg.jpg';
import colores from '../styles/colors';
import port from '../assets/portyt.png'

const EjesModal = ({ open, handleClose }) => {
	const classes = useStyles();

	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			>
				<AppBar className={classes.appBar}>
					<Toolbar style={{backgroundColor: colores.mainLightfull}}>
						<IconButton
							edge='start'
							color={colores.mainDarkfull}
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Ejes De Campaña
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{ padding: 40, paddingTop: 40 }}>
					<ContentEjes />
				</div>
			</Dialog>
		</div>
	);
};

const ContentEjes = () => {
	return (
		<Grid container spacing={3}>
			<Grid item xs={12}>
				<Paper style={{height: 250, backgroundImage: `url(${port})`, backgroundPosition: 'top', backgroundSize: 'cover'}}>
					
				</Paper>
			</Grid>
			{ejesC.map((eje) => (
				<Grid item xs={3}>
					<CardEjes title={eje.name} img={eje.img} />
				</Grid>
			))}
		</Grid>
	);
};

const CardEjes = ({ title, img }) => {
	return (
		<Card elevation={4} style={{width: 350, textAlign: 'center', display: 'flex', flexWrap: 'wrap'}}>
			<CardActionArea>
				<CardMedia
					style={{ height: 200, margin: 20, borderRadius: 15 }}
					image={img}
					title='Contemplative Reptile'
				/>
				<CardContent>
					<Typography gutterBottom variant='h5' component='h2'>
						{title}
					</Typography>
					<Typography variant='body2' color='textSecondary' component='p'>
					Trabajo a través de diferentes proyectos y campañas en la construcción de una conciencia colectiva en materia de reciclaje, 
					reutilización y cuidado de nuestro ambiente, como eje fundamental para el progreso de la sociedad santafesina.

					</Typography>
				</CardContent>
			</CardActionArea>
			<CardActions>
				<Button size='small' color='primary'>
					Iniciar Inscripcion
				</Button>
				<Button size='small' color='primary'>
					+ Info
				</Button>
			</CardActions>
		</Card>
	);
};

const ejesC = [
	{ name: 'Medio Ambiente', img: ecoAm },
	{ name: 'Obras públicas', img: obr },
	{ name: 'Género', img: gen },
	{ name: 'Espacios Publicos y Barrios', img: noi },
];

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	appBar: {
		position: 'relative',
	},

	title: {
		marginLeft: 5,
		color: colores.mainDarkfull
	},
}));

export default EjesModal;
