import React, { forwardRef } from 'react';
import { Grid, IconButton, makeStyles } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import gen from '../assets/ejes/genn.jpg';
import obr from '../assets/ejes/obr.png';
import ecoAm from '../assets/ejes/maff.png';
import noi from '../assets/noimg.jpg';
import colores from '../styles/colors';
import port from '../assets/portyt.png'

const ProyectosModal = ({ open, handleClose }) => {
    const classes = useStyles();

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar style={{ backgroundColor: colores.mainLightfull }}>
                        <IconButton
                            edge='start'
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label='close'
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant='h6' className={classes.title}>
                            Proyectos Presentados en HCSF
                        </Typography>
                    </Toolbar>
                </AppBar>
                <div style={{ padding: 40, paddingTop: 40 }}>
                    <ContentEjes />
                </div>
            </Dialog>
        </div>
    );
};

const ContentEjes = () => {
    return (
        <Grid container spacing={3}>
            
                {proyectos.map((proyect) => {

                    return <Grid item xs={6}> <CardProyect {...proyect} /></Grid>
                })}
            

        </Grid>
    );
};

const CardProyect = ({ name, text, img }) => {
    return (
        <Card style={{height: 300,display: 'flex'} }><CardMedia><img src={img} alt={name} style={{width: 350, height: '100%'}} />
            </CardMedia>
            <CardContent>
            <Typography gutterBottom variant='h5' component='h2'>
            {name}
            </Typography>
            <Typography gutterBottom variant='body2' component='body2'>
                {text}
            </Typography>
            </CardContent>
            </Card>
    );
};

const proyectos = [
{ name: 'proyecto Uno', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
 { name: 'proyecto Dos', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi }, 
 { name: 'proyecto Tres', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Cuatro', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Cinco', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Seis', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Siete', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
]

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
    appBar: {
        position: 'relative',
    },

    title: {
        marginLeft: 5,
        color: colores.mainDarkfull
    },
}));

export default ProyectosModal;
